@media (min-width: 1200px) and (max-width: 1910px) {
/* .container-fluid{width:60%;} */
}

@media (min-width: 992px) and (max-width: 1199px) {
.container-fluid{width:90%;}

}

@media (min-width: 768px) and (max-width: 991px) {
.logo img {
  float: left;
  margin: 20px auto !important;
  width: 360px;
}
  p.logo-text {
float: right;
  font-size: 45px;
  line-height: 1.4;
  font-weight: 700;
  color: #3e3934;

  margin-top: 13px;
}
ul.points li img {
  margin-bottom: 10px;
}
.container-fluid{width:90%;}


}

@media (min-width: 320px) and (max-width: 767px) {
.white-box {
  text-align: center;
}
.logo img {
  margin: 20px auto !important;
  width: 270px;
  float: none;
}
p.logo-text {
  text-align: center;
  font-size: 35px;
  line-height: 1.3;
  font-weight: 700;
  color: #3e3934;
  margin-top: 13px;
  margin-bottom: 30px;
}
.phone-outer input {
  width: 28.6% !important;
}
ul.points li img {
  margin-bottom: 10px;
}
.container-fluid{width:90%;}
}

